import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import { images, generateQuote, logo } from './helpers.js';
import '../node_modules/react-image-gallery/styles/css/image-gallery.css';
import './App.css';
import { FormControl, InputGroup, Alert, Button, Modal } from 'react-bootstrap';
import LoadingOverlay from 'react-loading-overlay';
const robodog = require('@uppy/robodog');
class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      message: '',
      imageIndex: 0,
      isHidden: true,
      modalIsOpen: false,
      returnedImage: '',
      quoteURL: '',
      error: false,
      loading: false
    };
    this.uploadQuote = this.uploadQuote.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.handleImage = this.handleImage.bind(this);
    this.handleModal = this.handleModal.bind(this);
    this.handleHidden = this.handleHidden.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.handleOriginalQuote = this.handleOriginalQuote.bind(this);
    this.prepareQuote = this.prepareQuote.bind(this);
  }

  async uploadQuote(blob) {
//    console.log(window.location.href + images[this.state.imageIndex].original);
    console.log(window.location.href);
    console.log(window.location.hostname);
    console.log(images[this.state.imageIndex].original);
    this.setState({loading: true});
    const resultPromise = robodog.upload([blob],{
      params: {
        auth: { key: 'ee723b70d56f11e8b7809f4882ea3f24' },
        "steps": {
          ":original": {
            "robot": "/upload/handle"
         },
          "imported": {
            "robot": "/http/import",
            "url": window.location.hostname + images[this.state.imageIndex].original
          },
          "watermarked": {
            "robot": "/image/resize",
            "imagemagick_stack": "v1.0.0",
            "watermark_size": "60%",
            "watermark_position": "bottom-left",
            "watermark_x_offset": 20,
            "use": {
              "steps": [
                { "name": "imported", "as": "base" },
                { "name": ":original", "as": "watermark" }
              ]
            }
          }
        }
      },
    waitForEncoding: true
    })
    resultPromise.then((bundle) =>{
      console.log(bundle);
      if(bundle.failed.length === 0){
        this.setState({
          modalIsOpen: true,
          loading: false,
          message: '',
          returnedImage: bundle.results[0].ssl_url
        });
      } else {
        this.setState({
          loading: false,
          modalIsOpen: false,
          message: '',
          error: true
        });
      }
    }) 
  }
  
  prepareQuote(){
    const genquote = generateQuote();
    const ctx = this.refs.canvas.getContext('2d');
    ctx.clearRect(0, 0, this.refs.canvas.width, this.refs.canvas.height);
    ctx.font = "30px Museo Sans";
    let words = genquote.split(" ");
    let lines = [];
    let currentLine = words[0];
    for (let i = 1; i < words.length; i++){
      let word = words[i];
      let width = ctx.measureText(currentLine + " " + word).width;
      if (width < 400) {
        currentLine += " " + word;
      } else {
        lines.push(currentLine);
        currentLine = word;
      }
    }
    lines.push(currentLine);
    ctx.strokeStyle = "black";
    ctx.lineWidth = 4;
    let linebreak = 60;
    for (let j = 0; j < lines.length; j++){
      ctx.strokeText(lines[j], 0, linebreak);
      ctx.fillStyle = "white";
      ctx.fillText(lines[j], 0, linebreak);
      linebreak+=30;
    }
    this.refs.canvas.toBlob((blob) =>{
      this.uploadQuote(blob);
    });
  }

  handleHidden(){
    this.setState({
      isHidden: !this.state.isHidden
    });
  }

  handleSelect(e){
    e.preventDefault();
    let key = e.target.name;
    let value = e.target.value;
    this.setState({[key]: value});
  }

  handleOriginalQuote(){
    this.setState({
      message: this.state.message.replace(/'/g, '’') + ' - Albert Einstein',
      isHidden: true
    });
    // this.generateImage();
  }

  handleImage(e){
    this.setState({imageIndex: e});
  }

  handleModal(){
    this.setState({modalIsOpen:false});
  }

  handleInput(e){
    this.setState({message: e.target.value})
  }

  render() {
    return (
      <LoadingOverlay
      active={this.state.loading}
      spinner
      text="Generating quote..."
      >
        <div className="App">
          <header className="App-header">
            <img src={logo} className="App-logo" alt="logo" />
            <div className="header-title">
              "Definitely the best webpage ever made." - Albert Einstein
            </div>
          </header>
          <div className="App-page">
            Generate your own Einstein quote! (Quotes possibly not said by Einstein)
          </div>
          <div className="App-gallery">
          <Modal id="modal-size" centered={true} show={this.state.modalIsOpen} onHide={this.handleModal}>
            <Modal.Body>
              <img 
                src={this.state.returnedImage}
                alt="generated-einstein"
                />
            </Modal.Body>
          </Modal>
          <ImageGallery onSlide={(e)=>this.handleImage(e)} items={images} showThumbnails={false} showFullscreenButton={false} showPlayButton={false}/>
          </div>
          <div className="App-form">
          <Button disabled={this.state.loading} onClick={()=> this.prepareQuote()} variant="outline-primary" >Generate a random quote!</Button>
          <div className="divider"/>
          <Button disabled={this.state.loading} onClick={()=> this.handleHidden()} variant="outline-primary" >Make your own quote!</Button>
          {!this.state.isHidden &&
          <div className="App-userinput">
          <InputGroup className="mb-3" onInput={(e)=>this.handleInput(e)}>
              <FormControl
                placeholder="Your quote"
                aria-label="Your quote"
                aria-describedby="basic-addon1"
                maxLength="50"
                disabled={this.state.loading}
              />
              <InputGroup.Append>
                <Button disabled={this.state.loading} onClick={(e)=>this.handleOriginalQuote(e)} variant="outline-primary">Submit</Button>
              </InputGroup.Append>
          </InputGroup>
          </div>
          }
          </div>
          <Alert variant="danger" defaultShow={this.state.error}>
            <Alert.Heading>It's not a bug...it's a feature</Alert.Heading>
            <p>Seems like an unexpected error occurred, contact the owner (https://github.com/mcallistertyler95/) of this site and let them know about it!</p>
          </Alert>
          <canvas id="canvas" ref="canvas" width={400} height={200}/>
          <footer className="App-footer">
            Icons made by <a href="https://www.freepik.com/" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0">CC 3.0 BY</a>
          </footer>
        </div>
      </LoadingOverlay>
    )
  }
}

export default App;